/* inject:components:less */
.et-navbar {
  position: relative;
  width: 100%;
  background-color: #fcfafa;
  height: 4em;
  font-size: 14px;
  line-height: 4em;
  transition: all 150ms ease-in-out;
}
.navbar-container {
  position: relative;
  height: inherit;
  width: 90%;
  max-width: 1024px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}
.navbar-container > * {
  height: inherit;
}
.navbar-container > * .link {
  color: #a03033;
  text-decoration: none;
  padding: 0 15px;
}
.navbar-container > .navbar-right:first-child {
  margin-left: auto;
}
.navbar-logo {
  height: inherit;
}
.navbar-logo img {
  height: 100%;
  padding-top: 10%;
  padding-bottom: 10%;
  padding-left: 20px;
  padding-right: 20px;
  max-height: 80px;
  box-sizing: border-box;
}
/*
*	Expand the navbar when to short
*/
@media (max-width: 720px) {
  .navbar-container {
    width: 100%;
  }
}
/*
*	Show only icons in links, remove navbar-list-right and show menu link
*/
/*
*	Shrink the navbar to 90%
*/
@media (max-width: 380px) {
  .et-navbar {
    font-size: 12.6px;
  }
}
/* endinject */
/* inject:partials:less */
.circular {
  width: 300px;
  height: 300px;
  border-radius: 150px;
}
.vcards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.vcards .vcard {
  display: flex;
  align-items: center;
  background-image: url("../../../assets/images/ETlogos/logo.svg");
  background-position: left;
  background-repeat: no-repeat;
  background-size: 7em;
  width: 300px;
  margin: 10px;
  padding: 2px;
}
.vcards .vcard .vcard-image {
  height: 80px;
  width: 80px;
  border-radius: 50%;
  background-size: cover;
  margin: 0px 10px;
  border: 1px solid #a03033;
}
.vcards .vcard span {
  font-weight: bold;
}
.paginator {
  display: flex;
  justify-content: center;
}
.login-container {
  margin: 0 auto;
  padding: 50px 0;
  max-width: 300px;
  text-align: center;
}
.login-container .login-btn-group-vertical > .btn {
  margin: 2px 0;
}
.login-container .login-header {
  margin: 25px 0 5px 0;
}
.login-container .login-content {
  padding: 15px 5px;
}
.login-container .login-separator {
  margin: 15px 0;
}
.login-container .login-separator:before,
.login-container .login-separator:after {
  background-color: #aaa;
  content: "";
  display: inline-block;
  height: 1px;
  position: relative;
  vertical-align: middle;
  width: 40%;
}
.login-container .login-separator > .separator-text {
  padding: 0 15px;
}
.noclass {
  background-color: #666;
}
